.modal-build {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;

  display: none;

  background-color: #ffffff;


  &--show {
    display: block;
  }
}

.modal-build__overlay {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;

  width: 100vw;
  height: 100vh;

  background-color: #ffffff;
  opacity: 0.9;
}

.modal-build__container {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 5;

  width: 320px;
  min-height: 336px;
  padding: 0 30px;

  background-color: #ffffff;
  border: 1px solid #c6c6c6;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  transform: translateX(-160px) translateY(-170px);
  opacity: 1;


  @media (min-width: $tablet) {
    top: 50%;
    left: 50%;

    width: 566px;
    padding: 0 50px;

    transform: translateX(-285px) translateY(-170px);
  }
}


.modal-build__title {
  margin: 20px auto;

  font-weight: 700;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;

  // @media (min-width: $tablet) {
  //   padding: 0;

  //   font-size: 30px;
  //   line-height: 57px;
  //   text-align: center;
  // }
}

.modal-build__descr {
  margin-top: 25px;
  padding-left: 60px;

  font-weight: 400;
  font-size: 15px;
  line-height: 30px;
  color: #444444;

  @media (min-width: $tablet) {
    padding: 0;

    font-size: 17px;
    text-align: center;
  }
}

.modal-build__list {
  @include list-reset();

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.modal-build__item {
  flex-shrink: 1;
  width: 200px;
  min-height: 150px;
  margin-left: 20px;

  border: 1px solid lightslategray;

  &:first-child {
    margin-left: 0;
  }

  &--technomarkt {
    background-image: url("../img/technomart-preview.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &--mishka {
    background-image: url("../img/mishka-preview.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.modal-build__link {
  display: inline-block;
  width: 100%;
  height: 100%;
  padding-top: 60px;

  text-align: center;
  color: transparent;

  background: transparent;

  &:hover,
  &:focus {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }
}
